.block-img{max-width: 100%; margin-bottom: $spacing; display: flex;}
.block-img__wrapper{text-decoration: none; color: inherit; text-align: center; overflow: hidden; display: block; box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 80px rgba(0, 0, 0, 0.06) inset;
  @include flex-container(column); height: 100%; width: 100%;
  *[class*="r_"] &{display: block; height: auto;}
  &:hover{color: inherit; text-decoration: none;}
}
.block-img__figure{position: relative; overflow: hidden; @include flex-grow();  width: 100%;
  & img{height: 100%; width: 100%; object-fit: cover;
    // pointer-events: none;
  }
  *[class*="r_"] &{
    &:before{content: ""; width: 1px; margin-left: -1px; float: left; height: 0; padding-top: 100%;}
    &:after {content: ""; display: table; clear: both;}
    & img{width: 100%; height: 100%;object-fit: cover; position: absolute; left: 0; top: 0;}
  }
  @each $side_1 in $sides {
    @if $side_1 == 'left' or $side_1 == 'right' {
      *[class*="r_"].ah_#{$side_1} & img{ object-fit: cover; object-position:#{$side_1}; }
      @each $side_2 in $sides {
        @if $side_2 == 'top' or $side_2 == 'bottom' {
          *[class*="r_"].av_#{$side_2} & img{ object-fit: cover; object-position:#{$side_2}; }
          *[class*="r_"].ah_#{$side_1}.av_#{$side_2} & img{ object-fit: cover; object-position:#{$side_1+' '+$side_2}; }
        }
      }
    }
  }

  .r_16-9 &:before{padding-top: 56.25%;}
  .r_2-1 &:before {padding-top: 50%;}
  .r_1-1 &:before {padding-top: 100%;}
  .r_1-2 &:before {padding-top: 200%;}
}
.block-img__title{width: 100%; padding: $spacing; background-color: body(font-color); color: body(background);}


